import { mapState } from 'vuex'
import tkiQrcode from "../../components/tki-qrcode"
export default { 
    components: {
        tkiQrcode
    },
    data () {
        return {
            currentBannerIndex: 0,
            myCardArr: [this.vCardBaseInfo,this.vCardBaseInfo,this.vCardBaseInfo],
            QRStr: '',
            currentTime: ''
        }
    },
    created() {
        this.getMyCard()
        this.creatQrcode()
    },
    watch: {
        currentTime() {                  
            this.changeQR()
        }
    },
    computed: {
        ...mapState(['vCardBaseInfo','userInfo'])
    },
    methods: {
        async getMyCard() {
            const res = await this.$api.myCardList()
            if (res.data.code === 0) {                
                this.creatQrcode()
            }
        },  
        getQRBack (res) {

        },    
        handleBannerChange (e) {
            this.currentBannerIndex = e.detail.current
        },           
        creatQrcode() {
            let temp = new Date()
            this.currentTime = new Date().getTime()
            this.$nextTick(() => {                    
                this.$refs.qrcode._makeCode()
            })
        },      
        changeQR () {   
            if (this.QRStr) {
                this.$refs.qrcode._clearCode()         
            }
            let code = 111
            let obj = {
                code
            }
            this.QRStr = JSON.stringify(obj)
        },
    },
}
